import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch
} from 'react-router-dom';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import ProductsPage from './pages/ProductsPage';
import ArticlePage from './pages/ArticlePage';
import NavigationBar from './navigation/NavigationBar';
import Contact from './Contact';
import NotFoundPage from './pages/NotFoundPage';
import CompanyTitleWithLogo from './navigation/CompanyTitleWithLogo';
import logo from './logo.svg';
import clogo from './icons/CompanyLogo.png';
import './App.css';
import tabs from './tabs.js';

function App() {
  return (
    <Router>
      <div className="main-content">
        <div className="c-tb-content">
          <NavigationBar company={<CompanyTitleWithLogo imgCls="c-company-logo" img={clogo}/>} /* titleCls="c-company-title" title="Behon3st" titleParentCls="c-company"*/
            navCls="c-main-navigation"
            navRole="navigation"
            tabs={tabs}/>
        </div>
        <div > 
          <Switch>
            <Route path="/" component={HomePage} exact/>
            <Route path="/about" component={AboutPage}/>
            <Route path="/products-list" component={ProductsPage}/>
            <Route path="/article/:name" component={ArticlePage}/>
            <Route component={NotFoundPage}/>
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
