import React, { useState } from 'react';
import './HomePage.css';
import Button from 'react-bootstrap/Button';
import landing from '../icons/landing.jpg';
import { Link } from 'react-router-dom';


const HomePage = () => {

    const handleClick = () => window.location.hash="products-list";

    return (
        <React.Fragment>
            <section className="c-home-section">
                <div className="c-home-section-top" style={{opacity: 1, transform: "translate(0px, 0px)"}}>
                    <div className="c-home-section-top-imgParent">
                        <img alt="home Us" className="c-home-section-img" src={landing}/>
                    </div>
                </div>
                <div className="c-home-section-bottom">
                    <div className="c-home-bg-white" style={{transform: "translate(0px, 0%)"}}></div>
                    <div className="c-home-section-text">
                        <div>
                            <p className="c-home-section-p">
                                We do our part for every 100 transactions!!! <br/>
                            </p>
                        </div>
                        <div className="c-home-section-botton-btn">
                            <Link className="btn btn-primary" to="/products-list">Discover Products</Link>
                        </div>
                    </div>
                </div>
            </section>
        </React.Fragment>
    );
}

export default HomePage;