import React from 'react';
import './AboutPage.css';
import about from './../icons/about.jpg';
import contactus from './../icons/contactus.jpg';
import ContactForm from '../components/ContactForm';

const AboutPage = () => (
    <React.Fragment>
        <section className="c-about-section">
            <div className="c-about-section-top" style={{opacity: 1, transform: "translate(0px, 0px)"}}>
                <div className="c-about-section-top-imgParent">
                    <img alt="About Us" className="c-about-section-img" src={about}/>
                </div>
            </div>
            <div className="c-about-section-bottom">
                <div className="c-about-bg-white" style={{transform: "translate(0px, 0%)"}}></div>
                <div className="c-about-section-text">
                    <div>
                        <h3 className="c-about-section-h3">Behon3st Group</h3>
                        <p className="c-about-section-p">Behon3st group is an Indian company producing export quality food items.
                            We are a group of like minded and dedicated individuals, trying to make a change in all aspects of food related business.
                        </p>
                    </div>
                </div>
            </div>
        </section>
        <section className="c-about-section">
            <div className="c-about-section-top" style={{opacity: 1, transform: "translate(0px, 0px)"}}>
                <div className="c-about-section-top-imgParent">
                    <img alt="Contact Us" className="c-about-section-img" src={contactus}/>
                </div>
            </div>
            <div className="c-about-section-bottom-contact">
                <div className="c-about-bg-white" style={{transform: "translate(0px, 0%)"}}></div>
                <div className="c-about-section-text">
                    <div>
                        <h3 className="c-about-section-h3">Contact Us</h3>
                        <ContactForm />
                    </div>
                </div>
            </div>
        </section>
    </React.Fragment>
)

export default AboutPage;