import React, {useState} from 'react';
import Carousel from 'react-bootstrap/Carousel';
import honeyArt from '../icons/honey-art.jpg';
import './ProductsPage.css';

const ProductsPage = () => {
    const [index, setIndex] = useState(0);
    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };

    return (
        <React.Fragment>
            <Carousel activeIndex={index} onSelect={handleSelect} className="c-products-page" indicators={false}>
                <Carousel.Item>
                    <img width="800" height="800"
                        className="d-block w-100"
                        src={honeyArt}
                        alt="Home"
                    />

                    <Carousel.Caption>
                        <p>
                            Honey (Madhu) is one of the five elixirs of life (Panchamrita).
                            <br/>
                            The food of Zeus and the twelve Gods of Olympus was honey in the form of nectar and ambrosia.
                            <br/>
                            John the Baptist lived for a long of time in the wilderness on a diet of locusts and honey.
                            <br/>
                            According to an-Nahl (the Bees), Muhammad strongly recommended honey for healing purposes.
                        </p>
                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>
        </React.Fragment>
    );
}

export default ProductsPage;