import React from 'react';
import './CompanyTitleWithLogo.css';

const CompanyTitleWithLogo = ({imgCls, img, titleCls, title, titleParentCls}) => (
    <>
        <div className="logo-top">
            <a href="/">
                <img alt="Behon3st Group" className={imgCls} src={img}/>
            </a>
            {title ? 
                <div className={titleParentCls}>
                    <h1 className={titleCls}>{title}</h1>
                </div>
            : ''}    
        </div>
    </>
);

export default CompanyTitleWithLogo;