import React from 'react';
import { NavLink } from 'react-router-dom';
import './NavigationBar.css';

const NavigationBar = ({company, navCls, navRole, tabs}) => (
  <>
    {company}
    <nav className={navCls} role={navRole}>
      {tabs.map((tab, key) => (
          <NavLink className={tab.cls} key={key} to={tab.to} role={tab.role} activeStyle={{textDecoration: 'overline', fontWeight: '900'}} exact>
            {tab.name}
          </NavLink>
      ))}
    </nav>
  </>
);

export default NavigationBar;